.App {
  text-align: center;
}

div{
  box-sizing: border-box;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background: grey;
  text-align: left;
}

.Component{
  margin-top: 20px;
  padding: 20px;
  background: skyblue;
  text-align: left;
}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid rgb(213, 213, 213);
}

.blog{
  display: flex;
  background: black;
  width:100%;
  color:white;
  padding-left:20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
